<template>
  <div class="hello" id="gorgeousBox">
    <ul class="image-container-box" id="box">
      <li class="image-box" v-for="item in imageData" :style="'backgroundImage : url(' + item.image + ')'">
        <h3 class="title">{{item.title}}</h3>
        <p class="introduce">{{item.introduce}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      imageData: [
        {title: '', introduce: '', image: require('../assets/_20170906235208.jpg')}
        // {title: '', introduce: '', image: require('../assets/_20170906235217.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235222.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235225.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235229.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235232.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235236.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235238.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235241.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235243.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235246.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235248.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235250.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235255.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235257.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235259.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235302.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235304.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235306.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235309.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235311.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235314.jpg')},
        // {title: '', introduce: '', image: require('../assets/_20170906235316.jpg')}
      ],
      liList: null,
      hList: []
    }
  },
  mounted: function () {
    var _this = this
    this.liList = document.getElementById('box').getElementsByTagName('li')
    this.getH()
    this.setPosition(0)
    document.getElementById('gorgeousBox').onscroll = function (e) {
      _this.setPosition(e.target.scrollTop)
    }
  },
  methods: {
    getH: function () {
      for (var i = 0, count = this.liList.length; i < count; i++) {
        this.hList.push(this.liList[i].offsetTop)
      }
    },
    setPosition: function (h) {
      for (var i = 0, count = this.liList.length; i < count; i++) {
        this.liList[i].style.backgroundPositionY = -(this.hList[i] - h) * 0.15 + 60 + 'px'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello{
    height: 100vh;
    overflow: scroll;
    -webkit-overflow-scrolling : touch;
  }
  .image-container-box{
    
  }
  .image-box{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    height:500px;
  }
</style>



